    </el-checkbox-group>

    </yd-cell-group>

     <!-- <yd-cell-group title="time">
        
        <div class="room_desc_item">
            <span class="item-title">鸡蛋 : </span>
            <yd-spinner v-model="egg" width="150px" height="40px" min="1" unit="1" max="5" class="item-count"></yd-spinner>
        </div>

        <div class="room_desc_item">
            <span class="item-title">鸡 : </span>
            <yd-spinner v-model="chicken" width="150px" height="40px" min="1" unit="1" max="5" class="item-count"></yd-spinner>
        </div>
            
    </yd-cell-group> -->

   
    
    <yd-cell-group title="Descrip your project">
