import { mapState ,mapGetters} from 'vuex'
const cityOptions = ['parttime', 'fulltime']
export default {
    components:{
        Radio, 
        RadioGroup,
        CellGroup,
        CellItem,
        Vqiniu,
        CitySelect,
        CheckListItem,
        CheckList,
        TextArea,
        
    },
    data () {
        return {
            tag:'',
            title:'',
            price:'',
            style:'',
            desc:'',
            checkboxGroup1: ['线上'],
            cities: cityOptions,
            egg:0,
            chicken:0

            
        }
    },
    computed: {
        ...mapState([
            'roomimg'
